import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams } from 'ionic-angular';
import { TextUtilProvider } from '../../providers/text-util/text-util';
import { Http } from '@angular/http';

@IonicPage()
@Component({
  selector: 'page-add-post',
  templateUrl: 'add-post.html',
})
export class AddPostPage {

  option: string = "Test";
  items = [
  ];
  post_type = "quiz";
  question;
  description;
  image;
  correct_option;
  data: any = {};
  isquiz:boolean=true;

  constructor(public navCtrl: NavController, public navParams: NavParams, public textUtil: TextUtilProvider, public http: Http) {
    this.image = "../assets/imgs/397.jpg";

    this.data.username = 'Love';
    this.data.response = '';

    this.http = http;
  }

  submit() {
    var link = 'http://localhost/api.php';
    var myData = JSON.stringify({ username: this.data.username });
    this.http.post(link, myData)
      .subscribe(data => {
        this.data.response = data["_body"]; 
        console.log(this.data.response);
      }, error => {
        console.log("Oooops!");
      });
  }
  addOption() {
    if (this.option.length != 0) {
      if (this.items.indexOf(this.option) == -1) {
        this.items.push(this.option);
        console.log(this.items.toString());
        this.option = "";
      } else {

        console.log("item already exists");
      }
    } else {

      console.log("item empty");
    }
  }
  deleteOption(item) {
    if (this.items.indexOf(item) != -1) {
      this.items.splice(this.items.indexOf(item), 1);
      console.log(this.items.toString());
    } else {

      console.log("item doesn't exist");
    }
  }
  change(index) {
    this.textUtil.change(index);
  }
  ionViewDidLoad() {
    console.log('ionViewDidLoad AddPostPage');
  }

  post() {
    var post_type = this.post_type;
    var question = this.question;
    var options = this.items;
    var description = this.description;
    var image = this.image;
    var correct_option = this.correct_option;
    var post = {
      "post_type": post_type,
      "question": question,
      "options": options,
      "description": description,
      "image": image,
      "correct_option": correct_option
    }
    console.log(post);
  }

}



